<template>
    <div class="song-audio">
        <audio  muted ref="player"
            :src="url"
            controls="controls"
            preload="true"
            @canplay="startPlay"
            @ended="ended"
            @timeupdate="timeUpdate"
        ></audio>
    </div>
</template>
<script>
import {mapGetters} from 'vuex';
export default {
    name: 'song-audio',
    computed: {
        ...mapGetters([
            'id',
            'url',
            'isPlay',
            'listOfSongs',     //
            'curTime',         //
            'changeTime',      //
            'autoNext',        //
            'volume'           //
        ])
    },
    watch: {
        //
        isPlay: function () {
            this.togglePlay();
        },
        //
        changeTime() {
            this.$refs.player.currentTime = this.changeTime;
        },
        //
        volume(val) {
            this.$refs.player.volume = val;
        }
    },
        methods: {
            //
            startPlay() {
                let player = this.$refs.player;
                this.$store.commit('setDuration',player.duration)
                //
                player.play();
                this.$store.commit('setIsPlay',true)
            },
            //
            ended() {
                this.$store.commit('setIsPlay',false);
                this.$store.commit('setCurTime',0);
                this.$store.commit('setAutoNext',!this.autoNext)
            },
            //
            togglePlay() {
                let player = this.$refs.player;
                if (this.isPlay) {
                    player.play();
                } else {
                    player.pause();
                }
            },
            //
            timeUpdate(){
                this.$store.commit('setCurTime',this.$refs.player.currentTime);
            }
        }

}

</script>

<style>
    .song-audio {
        display: inline;
      visibility: hidden;
    }
</style>
