<template>
    <div>
        <div class="signUp">
            <div class="signUp-head">
                <span></span>
            </div>
            <el-form :model="loginForm" ref="loginForm" label-width="70px" class="demo-ruleForm" :rules="rules">
                <el-form-item prop="username" label="">
                    <el-input v-model="loginForm.username" placeholder=""></el-input>
                </el-form-item>
                <el-form-item prop="password" label="">
                    <el-input type="password" v-model="loginForm.password" placeholder=""></el-input>
                </el-form-item>

                <div class="login-btn">
                    <el-button @click="goSignUp"></el-button>
                    <el-button type="primary" @click="handleLoginIn"></el-button>
                </div>
            </el-form>
        </div>
    </div>
</template>
<script>
    import {mixin} from '../mixins'
    import {loginIn} from '../api/index'

    export default {
        name: 'LoginIn',
        mixins: [mixin],
        components: {

        },
        data() {
            return {
                loginForm: {
                    username: '',       //
                    password: '',       //
                },
                rules: {
                    username: [
                        { required: true, trigger: 'blur',message: '' }
                    ],
                    password: [
                        { required: true, trigger: 'blur',message: '' }
                    ]
                }
            }
        },
        mounted() {
            this.changeIndex('');
        },
        methods:{
            handleLoginIn(){
                let _this = this;
                let params = new URLSearchParams();
                params.append('username',this.loginForm.username);
                params.append('password',this.loginForm.password);
                loginIn(params)
                    .then(res => {
                        if(res.code == 1){
                            /*_this.notify('','success');*/
                          alert('');
                            _this.$store.commit('setLoginIn',true);
                            _this.$store.commit('setUserId',res.userMsg.id);
                            _this.$store.commit('setUsername',res.userMsg.username);
                            _this.$store.commit('setAvator',res.userMsg.avator);
                            setTimeout(function(){
                                _this.changeIndex('');
                                _this.$router.push({path: '/'});
                            },2000);
                        }else{
                            /*_this.notify('','error');*/
                          alert('')
                        }
                    })
                    .catch(err => {
                       /* _this.notify('','error');*/
                    })
            },
            goSignUp(){
                this.changeIndex('');
                this.$router.push({path: '/register'});
            },
            changeIndex(value){
                this.$store.commit('setActiveName',value);
            },
        }
    }
</script>

<style  scoped>

    .signUp {
        position: center;
        top: 90px;
        background-color: #e0efff;
        border-radius: 10px;
        width: 350px;
        margin-left: 500px;
        padding: 100px 100px;
    }

    .signUp-head {
        text-align: center;
        margin-bottom: 10px;
        font-size: 20px;
        font-weight: 600;
    }

    .login-btn {
        justify-content: space-between;
        align-items: stretch;
        flex-direction: row;
        flex-wrap: nowrap;
        display: flex;
    }
    .login-btn button{
        display: block;
        width: 50%;
    }
</style>
